import React,{ useState} from 'react';
import styles from "./options.css"
const OptionS = ({ id,  title, slug,serarching }) => {
    const [ss, selet] = useState(true);
    const classChangr = (arg) => {
      let changer = document.getElementsByClassName("optId");
      changer[0].classList.remove("Blued");
      let sele = document.getElementsByClassName("opt");
      for (let m = 0; m < sele.length; m++)
        if(sele[m].innerHTML == arg)  { (sele[m].classList.add("Blued"))} 
        else{ (sele[m].classList.remove("Blued"))};
    };
  
    return (
      <div
        key={id}
        className={"opt"}
        onClick={(e) => {
          selet(false); 
          serarching(slug);
          classChangr(e.target.innerText);
        }}
      >
        {title}
      </div>
    );
  };
 
export default OptionS;