import React from 'react';
import styles from "./topbar.module.css"
const Topbar = () => {
  return (<nav className={styles.nav}>
    <div>
      <div>
        <pre><span className={styles.edyoda}>EDYODA</span>            <span className={styles.dropdownText}>Explore Categories  <i id="dropdown-icon" class="fas fa-chevron-down"></i></span>
          <p className={styles.stories}>Stories</p></pre>
      </div>
    </div>

    <div>
      <p>
        EdYoda is free learning and knowledge<br></br> sharing platform for techies
      </p>
      <div>
        <button>Go To Main Website</button>
      </div>
    </div>
  </nav>);
}

export default Topbar;