import React from 'react';
import styles from './card.module.css';

const Card = ({ small_image, description, authorname, posted_on, title }) => {
    return (
        <div className={styles.card}>
            <div>
                <img src={small_image}></img>
            </div>
            <div className={styles.descdiv}>
               <div><h2>{title}</h2></div>
               <p className ={styles.authop}><span className={styles.author}>{authorname}</span>  <span className ={styles.date}> | {posted_on}</span></p>
              <div><p className={styles.descp}>{description}</p></div>
            </div>
        </div> 
             
    )}
                export default Card;