// https://api.edyoda.com/v1/blog/
// https://api.edyoda.com/v1/blog/postcategories/
// https://api.edyoda.com/v1/blog/artificial-intelligence/
import React from 'react';
import{ useEffect, useState } from "react";
import Opt from "./Components/options/index";
import Card from "./Components/card/card";
import Topbar from "./Components/Topbar/index";
import axios from "axios";
import "./styles.css";

const Homepage = () => {
  const [optInt, settedOption] = useState([]);
  const [mycard, setCard] = useState([]);

  const adder = (r) => {
    console.log("entered");
    let changer = document.getElementsByClassName("optId");
    changer[0].classList.add("Blued");
    let itemgetter = document.getElementsByClassName("opt");
    for (let x = 0; x < itemgetter.length; x++)
     {itemgetter[x].classList.remove("Blued")};
  };

  useEffect(() => {
    axios.get("https://api.edyoda.com/v1/blog/postcategories/").then((res) => {
      settedOption(res.data);
    });
    axios.get("https://api.edyoda.com/v1/blog/").then((res) => {
      setCard(res.data);
    });
  }, []);

  const handle = (a) => {
    if (a === "all") {
      const linkone = "https://api.edyoda.com/v1/blog/";
      axios.get(linkone).then((re) => {
        setCard(re.data);
      });
    } else {
      const link = "https://api.edyoda.com/v1/blog/" + a + "/";
      axios.get(link).then((re) => {
        setCard(re.data.posts);
      });
    }
  };

  return (
    <div className="home">
      <Topbar></Topbar>
      <div className="optC">
    <div>  <i className="filterIcon" class="fas fa-filter"></i> <span className="filterText"> Filter by Category</span></div>
        <div
          className="optId"
          onClick={() => {
            handle("all");
            adder();
          }}
        >
          All
        </div>
        {optInt.map((item) => (
          <Opt serarching={(e) => handle(e)} {...item} />
        ))}
      </div>
      <div className="cardContainer">
        {mycard.map((items) => <Card {...items} />)}
      </div>
    </div>
  );
};

export default Homepage;
